import { h, Component } from 'preact';
import { Link } from 'preact-router/match';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import linkState from 'linkstate';
import { route } from 'preact-router';
import { Text, Localizer } from 'preact-i18n';

import style from './style.css';


const initialState = {
    menu: {
		profile: false,
		main: false,
	},
	searchText: '',
};

@observer
class Navigation extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	toggleDropdown = (e) => {
		const { menu } = this.state;
		let menuName = e.target.dataset.name;
		if (!menuName && e.target.parentNode) {
			menuName = e.target.parentNode.dataset.name;
		}
		if (menuName) {
			menu[menuName] = !menu[menuName];
		}
		this.setState({ menu });
	}

	toggleDarkmode = (e) => {
        const { appState } = this.props.stores;
		appState.toggleDarkmode();
		this.toggleDropdown(e);
	}

	toggleViewmode = (e) => {
        const { appState } = this.props.stores;
		let mode = e.target.dataset.mode;
		if (!mode && e.target.parentNode) {
			mode = e.target.parentNode.dataset.mode;
		}
		appState.toggleViewmode(mode);
		this.toggleDropdown(e);
	}

	reloadApp = () => {
		window.location.reload(true);
	}

	adminLoadAll = async (e) => {
        const { userStore } = this.props.stores;
        await userStore.getInfo(true);
	}

	changeLanguage = (e) => {
		const lang = e.target.value;
		console.log('changeLanguage', lang);
		const { appState, userStore } = this.props.stores;
		const { user = {} } = userStore;
		if (user.email) {
			userStore.setLanguage({ email: user.email, language: lang });
		}
		appState.setLanguage(lang);
	}

	search = () => {
		const { searchText } = this.state;
		route(`/search/${searchText}`);
	}

    render() {
		const { menu, searchText } = this.state;
        const { appState, userStore } = this.props.stores;
        const { darkmode, viewmode, isCordova } = appState;
		const { allUsers, isAdmin, user, notifications = [] } = userStore;
		const unreadNotificatins = notifications.filter(e => !e.read);
		const unread = unreadNotificatins.length || 0;
		const { language = 'en' } = user;

		return (
			<nav class='navbar navbar-expand-lg navbar-themed fixed-top py-1' style='z-index: 99999;'>
				<a class='navbar-brand' href='/race/'>
					{/* <img src={`./assets/logo-jul-2020-v2-${darkmode ? 'black' : 'white'}.png`} height='30' class='position-absolute' style='top: 8px; left: 6px' /> */}
					<img src={`./assets/homerun-race-logo-2-${darkmode ? 'white-' : ''}small.png`} height='37' class='position-absolute' style='top: 5px; left: 6px' />
					<span class='font-weight-normal' style='margin-left: 30px; font-size: 20px;'>Homerun Race</span>
				</a>

				<button class='navbar-toggler' type='button'
					data-name='main'
					onClick={this.toggleDropdown}
				>
					<span class='navbar-toggler-icon' />
				</button>

				<div class={`collapse navbar-collapse ${menu.main ? 'show' : ''}`} id='navbarSupportedContent'>
					<ul class='navbar-nav mr-auto ml-5'>
						<li class='nav-item'>
							<Link className='nav-link' activeClassName={'active'} href='/race'
								data-name='main'
								style='font-size: 1.0em !important;'
								onClick={this.toggleDropdown}
							><i class='fas fa-flag-checkered' /> <Text id='header.races'>Løpene</Text></Link>
						</li>
						<li class='nav-item'>
							<Link className='nav-link' activeClassName={'active'} href='/race/support'
								data-name='main'
								style='font-size: 1.0em !important;'
								onClick={this.toggleDropdown}
							><i class='far fa-question-circle' /> <Text id='header.support'>Support</Text></Link>
						</li>
						<li class='nav-item'>
							<Link className='nav-link' activeClassName={'active'} href='/race/news'
								data-name='main'
								style='font-size: 1.0em !important;'
								onClick={this.toggleDropdown}
							><i class='fas fa-quote-right' /> <Text id='header.news'>Nyheter</Text></Link>
						</li>
						{/* <li class='nav-item'>
							<Link className='nav-link' activeClassName={'active'} href='/'
								data-name='main'
								onClick={this.toggleDropdown}
							><i class='fas fa-dog' /> <Text id='header.backtoapp'>The Musher</Text></Link>
						</li> */}
						{/* <li class='nav-item'>
							<Link className='nav-link' activeClassName={'active'} href='/news'
								data-name='main'
								onClick={this.toggleDropdown}
							><i class='fas fa-quote-right' /> <Text id='header.news'>Nyheter</Text></Link>
						</li> */}
						{/* <li class='nav-item d-block d-lg-none'>
							<Link className='nav-link' activeClassName={'active'} href='/howto'
								data-name='main'
								onClick={this.toggleDropdown}
							><i class='fas fa-quote-right' /> <Text id='header.howto'>Hvordan gjør man det?</Text></Link>
						</li> */}
					</ul>

					<ul class='navbar-nav ml-auto '>
						{/* <li class='nav-item mr-2'>
							<Link className='nav-link' activeClassName={'active'}
								href='/users/notifications'
								data-name='main'
								onClick={this.toggleDropdown}
							>
								<i class='fas fa-bell mr-1' />
								{unread > 0 && <>
									<span class='badge badge-pill badge-danger'>{unread > 9 ? '9+' : unread}</span>
								</>}
							</Link>
						</li> */}
						<li class='nav-item mr-2'>
							<div class='form-row align-items-center'>
								<div class='col-auto my-1' style='width: 80px;'>
									<select class='custom-select'
										style='background-color: inherit; border: none; padding: 0; height: 1.1em; font-size: 0.9em;'
										onInput={this.changeLanguage}
									>
										<option value='no' selected={language === 'no'}>🇳🇴 NO</option>
										<option value='en' selected={language === 'en'}>🇬🇧 EN</option>
										<option value='es' selected={language === 'es'}>🇪🇸 ES</option>
										<option value='de' selected={language === 'de'}>🇩🇪 DE</option>
	                                    <option value='fr' selected={language === 'fr'}>🇫🇷 FR</option>
	                                    <option value='ru' selected={language === 'ru'}>🇷🇺 RU</option>
	                                    <option value='se' selected={language === 'se'}>🇸🇪 SE</option>
									</select>
								</div>
							</div>
						</li>
						<li class='nav-item d-block d-lg-none'>
							<Link className='nav-link' activeClassName={'active'}
								data-name='main'
								onClick={this.toggleDarkmode}
							>
								<i class={`${darkmode ? 'fas text-warning' : 'far'} fa-moon`} /> <Text id='header.darkmode'>Nattmodus</Text>
							</Link>
						</li>
						<li class='nav-item mr-2 d-none d-lg-block'>
							<button class='btn btn-link'
								data-name='profile'
								onClick={this.toggleDarkmode}
							>
								<i class={`${darkmode ? 'fas text-warning' : 'far'} fa-moon`} />
							</button>
						</li>
						{/* <li class='nav-item ml-auto d-none d-lg-block'>
							<form class='form-inline my-2 my-lg-0'>
								<Localizer>
									<input class='form-control form-control-sm mr-sm-2' type='search' placeholder={<Text id='header.search-placeholder'>Søk etter det meste...</Text>}
										value={searchText}
										onInput={linkState(this, 'searchText')}
									/>
								</Localizer>
								<button class='btn btn-sm btn-outline-success my-2 my-sm-0' type='button'	
									onClick={this.search}
								><Text id='header.search'>Søk</Text></button>
							</form>
						</li> */}
					</ul>

				    

				</div>
			</nav>
		);
	}
}

